import React, { Component } from 'react';
import {connect} from 'react-redux'
import  ReactPaginate from 'react-paginate';
import axios from 'axios';
import {BrowserRouter, Link, Route, Switch} from "react-router-dom";




class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { let{count,total}=this.props;
        return ( 
            <div className="header">
        <ul className="list">
        <li className="item"><Link to="/ecs" >HOME</Link></li>
        <li className="item"><Link to="/ecs/checkout" >CHECKOUT</Link></li>
        <li className="item"><Link to="/ecs/search" >SEARCH</Link></li>
        <li className="item"> <div className="svg"><svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-cart" viewBox="0 0 16 16">
        <path d="M0 1.5A.5.5 0 0 1 .5 1H2a.5.5 0 0 1 .485.379L2.89 3H14.5a.5.5 0 0 1 .491.592l-1.5 8A.5.5 0 0 1 13 12H4a.5.5 0 0 1-.491-.408L2.01 3.607 1.61 2H.5a.5.5 0 0 1-.5-.5zM3.102 4l1.313 7h8.17l1.313-7H3.102zM5 12a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm7 0a2 2 0 1 0 0 4 2 2 0 0 0 0-4zm-7 1a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm7 0a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"/>
      </svg></div><p className="count">{count}</p>
      <p className="countt">CART VALUE:RS.{total}</p>
        </li>
        


      
      

        
        </ul>
        </div>


     );
    }
}
 
const mapStatetoProps=(store)=>({
     count:store?.cart.length,
      cartproducts:store?.cart,
      total:  store?.cart?.reduce((amount,item)=>item.price+amount,0) })
    
export default connect(mapStatetoProps,null)( Header);
